.mask {
    width: 100%;
    height: 90vh;
    position: relative;
}

.intro-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color:#000;
    opacity: 0.8;
}
.intro {
    width: 100%;
    height: 100%;
}

.intro .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.intro .content h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
    color: #944000;
}
.intro .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
    color: #944000;
}

.content .btn{
    margin: 1rem 0.2rem;
}

@media screen and (max-width: 1024px){
    .intro .content h1{
        font-size: 3rem;
        padding: 0.6rem 0 1.5rem;
    }
    .intro .content p{
        font-size: 1.2rem;
    }
}